<style lang="less" scoped>
@import "../assets/less/list.less";
</style>

<template lang="html">
<div id="main-p">
    <base-bannerimg :img-url="parentNode.timg" :nodename="parentNode.name" :remark="parentNode.remark"></base-bannerimg>
  <div class="node-menu">
      <div class="node-menu-c">
          <router-link :to="d.link"  v-for="(d,index) in menuData">{{d.name}}</router-link>
      </div>
  </div>
  <div class="mainimglist">
    <div class="maintextlist">
      <div class="left">
          <list-text :param="param"></list-text>
      </div>
      <div class="right">
         <part-right :topnode="node.id" :nodename="node.name"></part-right>
      </div>
      <div style="clear:both;"></div>
    </div>
  </div>
</div>

</template>

<script>
import listText from "../components/list-text/list.vue";
import partRight from "../components/part-right/index";

export default {
  data() {
    return {
      menuData: [],
      param: {},
      node: {},
      parentNode: {}
    };
  },
  methods: {
    getSister: async function(nodeid) {
      const sister = await this.$tools.httpAgent("/api/tree/sister", "post", {
        nodeid: nodeid
      });
      this.menuData = sister.data.nodelist;
    },
    async getNode(nodeid) {
      const doc = await this.$tools.httpAgent(
        "/api/tree/get",
        "post",
        "id=" + nodeid
      );
      this.node = doc.data;
      this.$tools.setSeo(doc.data.name, doc.data.keyword, doc.data.seodes);
    },
    getDataEvent: function(index) {
      const item = index.split("-");
      this.param = {
        cp: 1,
        mp: item[2].replace("list", ""),
        nodeid: item[1],
        istp: true
      };
    },
    getParent: async function(nodeid) {
      const doc = await this.$tools.httpAgent("/api/tree/parent", "post", {
        id: nodeid
      });
      this.parentNode = doc.data;
    }
  },
  components: {
    "list-text": listText,
    "part-right": partRight
  },
  async mounted() {
    const param = this.$route.params;
    const nodeid = param.nodeid;
    const cp = param.cp ? param.cp : 1;
    const mp = param.mp ? param.mp : 10;
    this.getSister(nodeid);
    this.getNode(nodeid);
    this.getParent(nodeid);
    this.param = {
      cp,
      mp,
      nodeid: nodeid,
      istp: true
    };
  },
  watch: {
    $route: function() {
      const param = this.$route.params;
      const nodeid = param.nodeid;
      const cp = param.cp ? param.cp : 1;
      const mp = param.mp ? param.mp : 10;
      this.getSister(nodeid);
      this.getNode(nodeid);
      this.getParent(nodeid);
      this.param = {
        cp: cp,
        mp,
        nodeid: nodeid,
        istp: true
      };
    }
  }
};
</script>
